;(function ($, APP) {
  APP.back = {}

  function init () {
    console.log('init back')
    var cont = $('#back')
    var front = $('#front')
    var svg = $('#back svg')

    var onClicked
    var disabled = false

    var inTransit = false
    var elements = svg.find('#uomini > image')

    elements.on('click', function (e) {
      onClicked()
    })

    var width_view = $('body').width()
    var width_content = $('#back').width()
    var width_content_front = $('#front').width()
    var rapp = width_content_front / width_content

    $(window).on('resize', function () {
      width_view = $('body').width()
      width_content = $('#back').width()
      width_content_front = $('#front').width()
      rapp = width_content_front / width_content
      cont.css({x: (width_content - width_view) * -1})
      front.css({x: (width_content_front - width_view) * -1})
    })

    new Impetus({
      source: '#back',
      boundX: [(width_content - width_view) * -1, 0],
      bounce: true,
      initialValues: [(width_content - width_view) / 2 * -1, 0],
      update: function (x, y) {
        if (!inTransit && !disabled) {
          cont.css({x: x})
          front.css({x: x * 1.65})
        }
      }
    })

    function restore (_d) {
      var d = _d || 0
      width_view = $('body').width()
      width_content = $('#back').width()
      cont.css({x: (width_content - width_view) * -1})
      front.css({x: (width_content_front - width_view) * -1})
      disabled = false
    }

    function begin (picked) {
      elements.css('display', 'none')
      $(elements.get(picked)).css('display', 'block')
      disabled = false

      inTransit = true

      front.transition({x: (width_content_front - width_view) / 2 * -1, delay: 100}, 2500, 'easeInOutQuart')
      cont.transition({x: (width_content - width_view) / 2 * -1,
        delay: 100,
        complete: function () {
          inTransit = false
        }}, 2500, 'easeInOutQuart')
    }

    function pick (picked) {
      elements.css('display', 'none')
      $(elements.get(picked)).css('display', 'block')
    }

    function show () {
      cont.show()
    }

    function hide () {
      cont.hide()
    }

    APP.back.onClicked = function (clb) {
      onClicked = clb
    }

    APP.back.disable = function (clb) {
      disabled = true
    }

    APP.back.elements = elements
    APP.back.restore = restore
    APP.back.begin = begin
    APP.back.pick = pick
    APP.back.hide = hide
    APP.back.show = show
  }

  APP.back.init = init

  // if (APP.version === undefined && $('#back')[0]) {
  //   setTimeout(function () {
  //     init()
  //   }, 1500)
  // }
})(window.jQuery, window.APP)
